* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
header {
  font-size: 3rem;
  text-align: center;
  font-weight: 900;
  font-family: sans-serif;
  padding: 10px;
  background-color: goldenrod;
}
.todo-header {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 2%;
  background-color: black;
  & input , & button{
    padding: .5% .5%;
    font-size: 1.2rem;
    margin: 1%;
    border-radius: 10px;
  }
  & button:hover {
    background-color: goldenrod;
    color: white;
    border: 2px solid white;
  }
  & button {
    border: 2px solid goldenrod;
  }
  & input:focus {
    box-shadow:  goldenrod 1px 1px 0px 3px};
    border: none;
}


.section {
  width: 100%;
  height: 60vh;
  & div {
    position: relative;
  }
  & img {
    border: 1px solid white;
    width: 100%;
    height: 76vh;
    position: absolute;
  }
  & h1 {
    color: white;
    position: absolute;
    font-size: 4rem;
    font-family: sans-serif;
    left: 30%;
    color: goldenrod;
    font-weight: 800;
  }
}
.todo-item {
  text-align: center;
  border-radius: 10px;
  display: inline-block;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  width: 15%;
  margin-left: 1.4%;
  height: 180px;
  margin-top: 10px;
  & h1 {
    margin:5% ;
  }
  & p {
    margin: 10% 0;
    font-size: 1.3rem;
  }
  & button {
    padding: 10px;
    margin: 10px 10px;
    font-size: 1rem;
    font-weight: 600;
    color: white;
    border: none;
    border-radius: 10px;
  }
}